const initialState = {
    isAuthenticated: false,
    user: null,
    loading: true,
    error: null, //       .
};

export default function authReducer(state = initialState, action) {
    switch (action.type) {
        case 'USER_LOADED':
            return {
                ...state,
                isAuthenticated: true,
                loading: false,
                user: action.payload, //   
            };
        case 'NOT_AUTHENTICATED':
            return {
                ...state,
                isAuthenticated: false,
                loading: false,
            };
        case 'LOGIN_SUCCESS':
            return {
                ...state,
                isAuthenticated: true,
                loading: false,
                user: action.payload, //  user   
            };
        case 'LOGOUT':
            return {
                ...state,
                isAuthenticated: false,
                user: null,
                loading: false,
                error: null,
            };
        case 'LOGIN_FAIL':
            return {
                ...state,
                isAuthenticated: false,
                loading: false,
                user: null,
                error: action.payload,
            };
        case 'CLEAR_ERRORS': // default  
            return {
                ...state,
                error: null,
            };
        case 'AUTH_ERROR':
            return {
                ...state,
                isAuthenticated: false,
                loading: false,
                user: null,
                error: action.payload || 'Authentication error',
            };
        default: // default  
            return state;
    }
}
